import React, { useState, useEffect } from 'react';
import { Relay } from '@signalwire/js';

function App() {
  const [client, setClient] = useState(null);
  const [call, setCall] = useState(null);
  const [devices, setDevices] = useState(null);
  const [speaker, setSpeaker] = useState(null);
  const [token, setToken] = useState('');
  const [projectID, setProjectID] = useState('');
  const [connected, setConnected] = useState(false);

  useEffect(() => {
    const initializeDevice = async () => {
      if (!projectID || !token) {
        return;
      }

      const c = new Relay({
        project: projectID,
        token: token,
      });

      c.on('signalwire.ready', (client) => {
        console.log('Connected to Relay');
        console.log(client);
        setConnected(true);
      });

      c.on('signalwire.error', (error) => {
        console.log('Error connecting to Relay');
        console.log(error);
        setConnected(false);
      });

      c.on('signalwire.notification', (notification) => {
        console.log(notification);
        if (notification.type === 'callUpdate') {
          console.log('callUpdate');
          const call = notification.call;
          console.log(call.id, call.direction, call.state);
          setCall(call);
        } else {
          console.log(notification.type);
        }
      });

      c.connect();

      const d = await c.getDevices();
      setDevices(d);

      setClient(c);
    };

    initializeDevice();

    // Cleanup function
    return () => {
      if (client) {
        client.disconnect();
      }
    };
  }, [projectID, token]);

  const handleConnect = () => {
    // Connect the client based on the entered Project ID and JWT
    setProjectID(projectID);
    setToken(token);
  };

  const handleMakeCall = async () => {
    if (client) {
      const call = await client.newCall({
        destinationNumber: '<enter phone number>',
        audio: true,
        video: false,
      });
      console.log(call);
      setCall(call);
    }
  };

  const handleAnswer = async () => {
    console.log('Answering call...');
    console.log(call.id, call.direction, call.state);
    const res = await call.answer();
    console.log('Answered...');
    console.log(call.id, call.direction, call.state);
    console.log(client.connected);

    await call.unmuteAudio();

    const successAudioOut = await call.setAudioOutDevice('default');
    console.log(successAudioOut);

    const successAudioIn = await call.setAudioInDevice('default');
    console.log(successAudioIn);

    console.log(res);

    setSpeaker({ speaker: client.speaker, audio_in: successAudioIn, audio_out: successAudioOut });
  };

  const handleHangup = async () => {
    console.log('Hanging up call...');
    console.log(call);
    const res = await call.hangup();
    console.log(res);
  };

  return (
      <div className="space-y-4">
        <div>Relay App</div>
        <div>
          <label>Project ID:</label>
          <input
              type="text"
              value={projectID}
              onChange={(e) => setProjectID(e.target.value)}
          />
        </div>
        <div>
          <label>JWT:</label>
          <input
              type="text"
              value={token}
              onChange={(e) => setToken(e.target.value)}
          />
        </div>
        <button onClick={handleConnect}>Connect</button>
        <div>
          {connected ? (
              <p className="text-green-500">Connected</p>
          ) : (
              <p className="text-red-500">Disconnected</p>
          )}
        </div>
        <div className="flex gap-4">
          <button onClick={handleMakeCall}>Make Call</button>
          <button onClick={handleAnswer}>Answer</button>
          <button onClick={handleHangup}>Hang up</button>
        </div>

        <div>
          Call:
          {call ? (
              <pre>{JSON.stringify(call.state, null, 2)}</pre>
          ) : (
              'no call'
          )}
        </div>

        <div>
          <div className="font-bold">client.speaker</div>
          <pre className="p-4 bg-secondary text-sm">
          {JSON.stringify(speaker, null, 2)}
        </pre>
        </div>

        <div>
          <div className="font-bold">Devices</div>
          <pre className="p-4 bg-secondary text-sm">
          {JSON.stringify(devices, null, 2)}
        </pre>
        </div>
      </div>
  );
}

export default App;
